@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    #top-navbar { 
        font-family: 'Open Sans', sans-serif;
        font-size: 1.1em;
        width: 100%;
        margin: 0;
        display: flex;
        position: fixed;
        padding: 1.5em 0;
        background-color: #2E6296;
        color: #BDD5EA;
    }
    
    #home-container li{
        color: white;
        font-size: 1.2em;
    }

    #home-container #menuBtn{
        display: none;
    }
    
    #ref-links{
        display: flex;
        margin: 0;
        padding: 0;
        margin-left: auto;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

@media only screen and (max-width: 600px) {
    #top-navbar { 
        font-family: 'Open Sans', sans-serif;
        font-size: 1.1em;
        width: 100%;
        margin: 0;
        position: fixed;
        padding: 1em 0 0 0;
        background-color: #2E6296;
        color: #BDD5EA;
    }

    #home-container {
        display: flex;
        justify-content: space-between;
    }

    #home-container .menu-icon{
        padding-right: 1em;
        font-size: 1.5em;
        color: #FE5F55;
        will-change: opacity;
        -webkit-transition: opacity 0.25s;
        -moz-transition: opacity 0.25s;
        -o-transition: opacity 0.25s;
        transition: opacity 0.25s;
    }

    #home-container .menu-icon:hover{
        cursor: pointer;
        opacity: 0.6;
    }

    #ref-links{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 0px;
        margin: 1em 0 0 0;
        font-size: 0.9em;
        overflow: hidden;
        padding: 0;
        -webkit-transition: height 0.25s;
        -moz-transition: height 0.25s;
        -o-transition: height 0.25s;
        transition: height 0.25s;
    }

}
