.double-skills-container{
    display: flex;
    font-size: 1.2em;
}

.double-skills-container .skill-container{
    flex: 1 1 0;
}

.double-skills-container .left-container{
    border-right: 1px solid black;
    margin-right: 1em;
    text-align: left;
}

.double-skills-container .right-container{
    text-align: right;
    border-left: 1px solid black;
    margin-left: 1em;
}