
footer p{
    font-size: 0.9em;
    padding: 0.5em 0;
    margin: 0;
}

footer a{
    color: black;
    text-decoration: none;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    footer{
        text-align: center;
        background-color: #E8E8E8;
        padding: 2em 0 1em 0;
    }

    footer i{
        margin: 0.2em 0.3em;
        font-size: 1.5em;
    }

    footer i:hover{
        cursor: pointer;
        opacity: 0.5;
    }
}

/** ---------------------------------- 
 *           SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    footer{
        text-align: center;
        background-color: #E8E8E8;
        padding: 0 0 2em 0;
    }

    #social-icons{
        margin: 0.2em 0;
    }

    footer i{
        margin: 0.5em 0.3em 0 0.3em;
        font-size: 1.5em;
    }
 }