.info-elem-container{
    display: flex;
    align-items: center;
    margin: 1em 0;
}

.info-elem-container p{
    margin-left: 1em;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    .info-elem-container{
        font-size: 1.2em;
    }

    .info-elem-container i{
        font-size: 1.4em;
    }
    
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    .info-elem-container{
        text-align: left;
        font-size: 1.1em;
    }
 }