/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    .project-container{
        width: 100%;
        text-align: left;
        margin: 3em 0;
    }
    
    .project-container i{
        font-size: 2em;
    }
    
    .project-container .date-container{
        border: 2px solid gray;
        border-radius: 10px 10px 0 0;
        border-bottom: none;
        width: 20%;
        display: flex;
        padding: 0.8em 0;
        margin-top: 1em;
        justify-content: center;
        align-items: center;
        background-color: #E8E8E8;
    }
    
    .project-container .date-container p{
        margin: 0 1em;
    }
    
    .project-container .content-container{
        display: flex;
        padding: 1em 1em;
        border: 2px solid gray;
        border-radius: 0 10px 10px 10px;
    }
    
    .project-container .content-container .text-container{
        flex-grow: 2;
        padding-right: 1em;
    }
    
    .project-container .content-container .text-container .description-container{
        display: flex;
    }
    
    .project-container .content-container .text-container .description-container p:last-child{
        padding-left: 0.7em;
        word-break: break-all;
    }
    
    .project-container .content-container .img-container{
        flex-grow: 0;
        flex-shrink: 0;
        flex-basis: 20em;
    }
    
    .project-container .content-container .img-container img{
        max-width: 100%;
        max-height: 100%;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    .project-container{
        margin: 2em 0;
    }

    .project-container i{
        font-size: 1.8em;
    }

    .project-container .date-container{
        display: flex;
        border: 2px solid gray;
        border-radius: 10px 10px 0 0;
        border-bottom: none;
        width: 80%;
        background-color: #E8E8E8;
        justify-content: center;
        align-items: center;
    }

    .project-container .date-container p{
        margin: 0;
        padding: 1em;
    }

    .project-container .content-container{
        padding: 1em 1em;
        border: 2px solid gray;
        border-radius: 0 0px 10px 10px;
    }

    .project-container .content-container .status-icon-wrap{
        display: flex;
        align-items: center;
    }

    .project-container .content-container .img-container img{
        max-width: 100%;
        max-height: 100%;
    }
}
