/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #personal-container{
        display: flex;
        text-align: center;
        flex-direction: column;
        padding: 0 10em;
        margin-top: 6em;
    }

    #content-container{
        display: flex;
        text-align: left;
        margin-top: 2em;
    }

    #content-container .info-header{
        text-align: center;
        font-family: 'Poppins', sans-serif;
        color: #08415C;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

@media only screen and (max-width: 600px) {
    #personal-container{
        padding: 2em;
        text-align: center;
    }

    #content-container .info-header{
        text-align: center;
        font-family: 'Poppins', sans-serif;
        color: #08415C;
    }
}