/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #areas-interest-container{
        padding: 0 10em;
        text-align: center;
    }

    #areas-interest-container .grid-container{
        display: grid;
        grid-template-columns: auto auto auto;
        margin: 2em 0;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #areas-interest-container{
        padding: 0 2em;
        text-align: center;
    }
}