@import url('https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap');

  /* removes blue background effect on click */ 
  input,  
  textarea,
  button,
  select,
  div, 
  a {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  
@media only screen and (min-width: 600px) {
  body{
    font-family: 'Raleway', sans-serif;
    max-width: 100%;
    margin: 0;
    min-width: 1300px;
  }
}

@media only screen and (max-width: 600px) {
  body{
    max-width: 100%;
    min-height: 50%;
  }
}