/** ---------------------------------- 
 *           COMMON STYLE
 *  ---------------------------------- */

li{
    list-style-type: none;
}

.link{
    color: white;
    font-size: 1em;
    padding: 0 2em;
    text-decoration: none;
    background-color:  #2E6296;
    border: none;
}

.link:active, .link:focus{
    outline: none;
}

.link:hover{
    cursor: pointer;
    color: #FE5F55;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    #right-links li{
        border-right: 2px solid #FE5F55;
    }

    #right-links li:last-child{
        border-right: none;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    li{
        margin: 0.5em 0;
    }
}