.grid-element{
    padding: 1em 1em;
}

.grid-element h3{
    margin: 0;
    color: #08415C;
}

.animate-donut-segment{
    -webkit-animation: anim 0.8s ease-out forwards;
    -moz-animation: anim 0.8s ease-out forwards;
    -ms-animation: anim 0.8s ease-out forwards;
    -o-animation: anim 0.8s ease-out forwards;
    animation: anim 0.8s ease-out forwards;
}

@-webkit-keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
}

@-moz-keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
}

@-o-keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
}

@keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
  }