/** ---------------------------------- 
 *           COMMON STYLE
 *  ---------------------------------- */

.panelChoosingTab{
    display: flex;
    font-size: 1.2em;
    color: #08415C;
}

#activeTab, #inactiveTab{
    flex: 1;
    cursor: pointer;
}

#activePanel{
    display: block;
}
#activeTab p {
    border-bottom: solid 2px #FE5F55;
}

#inactivePanel{
    display: none;
    border: none;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #portfolio-container{
        padding: 0 10em;
        text-align: center;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #portfolio-container{
        padding: 0 2em;
        text-align: center;
    }
}
