@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** ---------------------------------- 
 *           COMMON STYLE
 *  ---------------------------------- */

li{
    list-style-type: none;
}

.link{
    color: white;
    font-size: 1em;
    padding: 0 2em;
    text-decoration: none;
    background-color:  #2E6296;
    border: none;
}

.link:active, .link:focus{
    outline: none;
}

.link:hover{
    cursor: pointer;
    color: #FE5F55;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    #right-links li{
        border-right: 2px solid #FE5F55;
    }

    #right-links li:last-child{
        border-right: none;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    li{
        margin: 0.5em 0;
    }
}
/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    #top-navbar { 
        font-family: 'Open Sans', sans-serif;
        font-size: 1.1em;
        width: 100%;
        margin: 0;
        display: flex;
        position: fixed;
        padding: 1.5em 0;
        background-color: #2E6296;
        color: #BDD5EA;
    }
    
    #home-container li{
        color: white;
        font-size: 1.2em;
    }

    #home-container #menuBtn{
        display: none;
    }
    
    #ref-links{
        display: flex;
        margin: 0;
        padding: 0;
        margin-left: auto;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

@media only screen and (max-width: 600px) {
    #top-navbar { 
        font-family: 'Open Sans', sans-serif;
        font-size: 1.1em;
        width: 100%;
        margin: 0;
        position: fixed;
        padding: 1em 0 0 0;
        background-color: #2E6296;
        color: #BDD5EA;
    }

    #home-container {
        display: flex;
        justify-content: space-between;
    }

    #home-container .menu-icon{
        padding-right: 1em;
        font-size: 1.5em;
        color: #FE5F55;
        will-change: opacity;
        -webkit-transition: opacity 0.25s;
        transition: opacity 0.25s;
    }

    #home-container .menu-icon:hover{
        cursor: pointer;
        opacity: 0.6;
    }

    #ref-links{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 0px;
        margin: 1em 0 0 0;
        font-size: 0.9em;
        overflow: hidden;
        padding: 0;
        -webkit-transition: height 0.25s;
        transition: height 0.25s;
    }

}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #profile-container{
        height: 80vh;
        padding-top: 7em;
        display: flex;
        align-items: center;
        padding: 3em 5em;
        background-color: #2E6296;
    }

    #profile-container .halfDivision{
        flex: 1 1;
    }

    #img-container{
        display: flex;
        max-height: 90%;
        max-width: 38%;
        justify-content: center;
    }

    #img-container img{
        max-height: 100%;
        max-width: 100%;
        border-radius: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }

    #profile-container .halfDivision p{
        font-family: 'Poppins', sans-serif;
        font-size: 6em;
        color: white;
        margin: 0;
    }

    #profile-container .halfDivision #mini-container p{
        font-family: 'Poppins', sans-serif;
        font-size: 1.1em;
        color: #FE5F55;
        padding: 0.5em 0;
    }

    #nameTitle{
        margin-left:10em;
    }

    #profile-pic{
        max-width: 100%;
        height: auto;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

@media only screen and (max-width: 600px) {
    #profile-container{
        height: 80vh;
        background-color: #2E6296;
        margin-bottom: 20vh;
    }
    
    #img-container{
        padding-top: 2em;
        max-width: 100%;
        display: flex;
        align-items: center;
    }

    #img-container img{
        max-height: 50%;
        max-width: 50%;
        border-radius: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }

    #nameTitle{
        font-family: 'Poppins', sans-serif;
        font-size: 3em;
        color: white;
    }

    #mini-container{
        font-size: 0.3em;
    }

    #nameTitle p{
        margin: 0;
    }
}
/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #personal-container{
        display: flex;
        text-align: center;
        flex-direction: column;
        padding: 0 10em;
        margin-top: 6em;
    }

    #content-container{
        display: flex;
        text-align: left;
        margin-top: 2em;
    }

    #content-container .info-header{
        text-align: center;
        font-family: 'Poppins', sans-serif;
        color: #08415C;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

@media only screen and (max-width: 600px) {
    #personal-container{
        padding: 2em;
        text-align: center;
    }

    #content-container .info-header{
        text-align: center;
        font-family: 'Poppins', sans-serif;
        color: #08415C;
    }
}
.info-elem-container{
    display: flex;
    align-items: center;
    margin: 1em 0;
}

.info-elem-container p{
    margin-left: 1em;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

@media only screen and (min-width: 600px) {
    .info-elem-container{
        font-size: 1.2em;
    }

    .info-elem-container i{
        font-size: 1.4em;
    }
    
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    .info-elem-container{
        text-align: left;
        font-size: 1.1em;
    }
 }
#personal-info-container{
    flex: 1 1;
}



#top-skills-container{
    flex: 1 1;
    color: '#08415C'
}

.skills-wrap{
    margin-top: 3em;
}
.double-skills-container{
    display: flex;
    font-size: 1.2em;
}

.double-skills-container .skill-container{
    flex: 1 1;
}

.double-skills-container .left-container{
    border-right: 1px solid black;
    margin-right: 1em;
    text-align: left;
}

.double-skills-container .right-container{
    text-align: right;
    border-left: 1px solid black;
    margin-left: 1em;
}
.grid-element{
    padding: 1em 1em;
}

.grid-element h3{
    margin: 0;
    color: #08415C;
}

.animate-donut-segment{
    -webkit-animation: anim 0.8s ease-out forwards;
    animation: anim 0.8s ease-out forwards;
}

@-webkit-keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
}

@keyframes anim {
    to {
      stroke-dashoffset: 125;
    }
  }
/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #areas-interest-container{
        padding: 0 10em;
        text-align: center;
    }

    #areas-interest-container .grid-container{
        display: grid;
        grid-template-columns: auto auto auto;
        margin: 2em 0;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #areas-interest-container{
        padding: 0 2em;
        text-align: center;
    }
}
/** ---------------------------------- 
 *           COMMON STYLE
 *  ---------------------------------- */

.panelChoosingTab{
    display: flex;
    font-size: 1.2em;
    color: #08415C;
}

#activeTab, #inactiveTab{
    flex: 1 1;
    cursor: pointer;
}

#activePanel{
    display: block;
}
#activeTab p {
    border-bottom: solid 2px #FE5F55;
}

#inactivePanel{
    display: none;
    border: none;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #portfolio-container{
        padding: 0 10em;
        text-align: center;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #portfolio-container{
        padding: 0 2em;
        text-align: center;
    }
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    .project-container{
        width: 100%;
        text-align: left;
        margin: 3em 0;
    }
    
    .project-container i{
        font-size: 2em;
    }
    
    .project-container .date-container{
        border: 2px solid gray;
        border-radius: 10px 10px 0 0;
        border-bottom: none;
        width: 20%;
        display: flex;
        padding: 0.8em 0;
        margin-top: 1em;
        justify-content: center;
        align-items: center;
        background-color: #E8E8E8;
    }
    
    .project-container .date-container p{
        margin: 0 1em;
    }
    
    .project-container .content-container{
        display: flex;
        padding: 1em 1em;
        border: 2px solid gray;
        border-radius: 0 10px 10px 10px;
    }
    
    .project-container .content-container .text-container{
        flex-grow: 2;
        padding-right: 1em;
    }
    
    .project-container .content-container .text-container .description-container{
        display: flex;
    }
    
    .project-container .content-container .text-container .description-container p:last-child{
        padding-left: 0.7em;
        word-break: break-all;
    }
    
    .project-container .content-container .img-container{
        flex-grow: 0;
        flex-shrink: 0;
        flex-basis: 20em;
    }
    
    .project-container .content-container .img-container img{
        max-width: 100%;
        max-height: 100%;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    .project-container{
        margin: 2em 0;
    }

    .project-container i{
        font-size: 1.8em;
    }

    .project-container .date-container{
        display: flex;
        border: 2px solid gray;
        border-radius: 10px 10px 0 0;
        border-bottom: none;
        width: 80%;
        background-color: #E8E8E8;
        justify-content: center;
        align-items: center;
    }

    .project-container .date-container p{
        margin: 0;
        padding: 1em;
    }

    .project-container .content-container{
        padding: 1em 1em;
        border: 2px solid gray;
        border-radius: 0 0px 10px 10px;
    }

    .project-container .content-container .status-icon-wrap{
        display: flex;
        align-items: center;
    }

    .project-container .content-container .img-container img{
        max-width: 100%;
        max-height: 100%;
    }
}

#email-container:focus, #email-container:active{
    outline: none;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #contact-container{
        text-align: center;
        padding-right: 2em;
        padding: 0 10em;
        background-color: #E8E8E8;
    }

    #contact-container h1{
        padding-top: 2em;
        color: #084C61;
    }

    #contact-container p{
        padding: 2em 0;
        margin: 0;
    }

    #email-container{
        display: inline-block;
        font-size: 1.3em;
        color: black;
        text-decoration: none;
        -webkit-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;
        padding: 3em 7em;
        border: 2px solid #084C61;
        background-color: #E8E8E8;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #contact-container{
        text-align: center;
        padding: 2em;
        background-color: #E8E8E8;
    }

    #contact-container h1{
        text-align: center;
    }

    #contact-container p{
        margin: 1.5em 0 0 0;
    }

    #email-container{
        display: inline-block;
        text-align: center;
        color: black;
        text-decoration: none;
        border: 2px solid #084C61;
        width: 100%;
        padding: 3em 0;
        background-color: #E8E8E8;
    }

    #email-container:hover{
        cursor: pointer;
    }
}

