  body{
    font-family: 'Raleway', sans-serif;
    max-width: 100%;
    margin: 0;
    min-width: 1300px;
  }
}

@media only screen and (max-width: 600px) {
  body{
    max-width: 100%;
    min-height: 50%;
  }
}
