#email-container:focus, #email-container:active{
    outline: none;
}

/** ---------------------------------- 
 *           DESKTOP STYLE
 *  ---------------------------------- */

 @media only screen and (min-width: 600px) {
    #contact-container{
        text-align: center;
        padding-right: 2em;
        padding: 0 10em;
        background-color: #E8E8E8;
    }

    #contact-container h1{
        padding-top: 2em;
        color: #084C61;
    }

    #contact-container p{
        padding: 2em 0;
        margin: 0;
    }

    #email-container{
        display: inline-block;
        font-size: 1.3em;
        color: black;
        text-decoration: none;
        transition: all 0.2s ease-in-out;
        padding: 3em 7em;
        border: 2px solid #084C61;
        background-color: #E8E8E8;
    }
}

/** ---------------------------------- 
 *          SMARTPHONE STYLE
 *  ---------------------------------- */

 @media only screen and (max-width: 600px) {
    #contact-container{
        text-align: center;
        padding: 2em;
        background-color: #E8E8E8;
    }

    #contact-container h1{
        text-align: center;
    }

    #contact-container p{
        margin: 1.5em 0 0 0;
    }

    #email-container{
        display: inline-block;
        text-align: center;
        color: black;
        text-decoration: none;
        border: 2px solid #084C61;
        width: 100%;
        padding: 3em 0;
        background-color: #E8E8E8;
    }

    #email-container:hover{
        cursor: pointer;
    }
}